.App{
  background-image: url("images/close-up-colors-design-1421253.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  min-height: 100vh;
  display: flex;
  font-size: 25px;
  font-family: 'Great Vibes', cursive;
  font-family: 'Srisakdi', cursive;
  font-family: 'Poiret One', cursive;

  flex-direction: column;
  align-items: center;
  padding: 100px;
}

.box{
  width: 25px;
  height:25px;
  border: 1px solid black;
}

.knittingBox{
  margin-top: 50px;
  display: flex;
}
.input {
  font-size: 20px;
background: rgba(255, 238, 245, 0.73);
  width: 50px;
padding: 10px;
  text-align: center;
  margin: 20px;
}